<template>
    <div ref="map" class="map" tabindex="0">

    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

import 'ol/ol.css';

import Map from 'ol/Map';
import OSM from 'ol/source/OSM';
import VectorSource from 'ol/source/Vector';
import TileLayer from 'ol/layer/Tile';
import VectorLayer from 'ol/layer/Vector';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
// import Icon from 'ol/style/Icon';
import CircleStyle from 'ol/style/Circle';
import { Fill, Stroke } from 'ol/style';
import Style from 'ol/style/Style';
import View from 'ol/View';
import { fromLonLat, toLonLat } from 'ol/proj';
import Geolocation from 'ol/Geolocation';
// import navIcon from './../../../../../assets/fourier a0.png';


export default {
    name: 'OpenLayersMap',
    data () {
        return {
            map: {},
            pointerFeature: null,
            geolocation: {},
            center: [11.41552, 45.50389],
            features: [],
            targetPlaceLayer: {},
        }
    },
    watch: {
        trackPosition (newValue) {
            this.geolocation.setTracking(newValue);
        },
        places: {
            handler (newValue) {
                if (newValue !== undefined) {
                    this.featuresFromPlaces();
                    console.log('editing')
                }
            },
            deep: true,
        }
    },
    computed: {
        ...mapState ([
            'focusPosition',
            'currentLocation',
            'places',
        ]),
    },
    methods: {
        ...mapMutations([
            'stopFollowingPosition',
            'updateLocation',
        ]),
        initMap () {
            this.pointerFeature = new Feature({
                geometry: new Point([0,0]),
            });
            this.pointerFeature.setStyle(
                new Style({
                    image: new CircleStyle({
                        radius: 8,
                        fill: new Fill({
                            color: '#3399CC',
                        }),
                        stroke: new Stroke({
                            color: '#fff',
                            width: 2,
                        }),
                }),
                    // image: new Icon({
                    //     anchor: [0.5, 46],
                    //     anchorXUnits: 'fraction',
                    //     anchorYUnits: 'pixels',
                    //     opacity: 1,
                    //     size: [100,100],
                    //     rotateWithView: true,
                    //     src: navIcon,
                    // })
                })
            );

            this.map = new Map({
                layers: [
                    new TileLayer({
                        source: new OSM(),
                    }),
                    new VectorLayer({
                        source: new VectorSource({
                            features: [
                                this.pointerFeature,
                            ],
                        }),
                    }),
                ],
                target: this.$refs['map'],
                view: new View({
                    center: fromLonLat([0,0]),
                    zoom: 15,
                }),
                controls: [],
            });


            this.geolocation = new Geolocation({
                projection: this.map.getView().getProjection(),
                trackingOptions: {
                    enableHighAccuracy: true,
                },
            });

            this.geolocation.on('change', () => {
                const view = this.map.getView();
                if (
                    view.getCenter()[0] != this.currentLocation.position.lon &&
                    view.getCenter()[1] != this.currentLocation.position.lat ||
                    view.getRotation() != this.currentLocation.heading
                ) {
                    if (this.focusPosition) {
                        this.focusOnPos();
                    }
                }
            });

            this.geolocation.on('change:position', () => {
                const position = this.geolocation.getPosition();
                this.updateLocation({
                    position: {
                        lon: position[0],
                        lat: position[1],
                    },
                });
                this.pointerFeature.getGeometry().setCoordinates(position);
            });
            this.geolocation.on('change:heading', () => {
                const h = this.geolocation.getHeading();
                this.updateLocation({
                    heading: (h === undefined) ? 0 : h,
                });
                this.pointerFeature.getGeometry().set('rotation', this.currentLocation.heading);
            })

            this.geolocation.setTracking(true);

            this.map.on('moveend',() => {
                console.log(toLonLat(this.map.getView().getCenter()));
                this.map.getView().unset('focusMove');
                this.getLocationName()
                    .then(name => { this.updateLocation({name}); });
            });
            this.map.on('movestart', () => {
                console.log(this.map.getView().get('focusMove'))
                if (!this.map.getView().get('focusMove')) {
                    this.stopFollowingPosition();
                }
            });
        },
        focusOnPos () {
            const view = this.map.getView();

            view.set('focusMove', true);
            view.animate(
                {center: Object.values(this.currentLocation.position)},
                {zoom: 17},
                {rotation: this.currentLocation.heading},
            );
        },
        focusPlace (place) {
            const view = this.map.getView();
            this.stopFollowingPosition();

            // view.set('programmaticMove', true);
            view.animate(
                {center: fromLonLat(Object.values(place.coords))},
                {zoom: 17},
                {rotation: 0},
            );
        },
        getLocationName () {
            // console.log('updating location name');
            /* Request reverse geocoding to OpenCageData API */
            var url = 'https://api.geoapify.com/v1/geocode/reverse';

            const [lon, lat] = toLonLat(this.map.getView().getCenter());
            var params = {
                ...{lon, lat},
                apiKey: 'f3796c82f94f4778a9aec8927013f929' // Opencagedata api key
            }
            // params = JSON.stringify(params);
            // console.log(params)
            url += '?' + ( new URLSearchParams(params) ).toString();
            return fetch(url, {credentials: 'include', headers: {'Access-Control-Allow-Origin': window.location.protocol+'//'+window.location.hostname}})
                // .then(r => console.log(r))
                .then(response => response.json())
                .then(r => {
                    const features = r.features.length > 0 ? r.features[0].properties : {};
                    const location_features = (({street, city, county, state, country}) => [street, city, county, state, country])(features);
                    return location_features.filter(n => n).slice(0,2).join(', ');
                });
        },
        featuresFromPlaces () {
            console.log(this.places)
            if (this.places.length == 0) {return;}
            this.map.removeLayer(this.targetPlaceLayer)
            this.features = []
            for (var id in this.places) {
                const place = this.places[id];
                if (place.title === '') {continue;}
                this.features.push(
                    new Feature({
                        geometry: new Point(fromLonLat(Object.values(place.coords)))
                    })
                )
                this.features[this.features.length-1].setStyle(
                    new Style({
                        image: new CircleStyle({
                            radius: place.found ? 4 : 6,
                            fill: new Fill({
                                color: place.found ? '#F92880' : '#00f030',
                            }),
                            stroke: new Stroke({
                                color: '#fff',
                                width: 2,
                            }),
                        }),
                    }));

                console.log(this.features)
            }
            this.targetPlaceLayer = new VectorLayer({
                source: new VectorSource({
                    features: this.features,
                }),
            });
            // this.targetPlaceLayer.setStyle(
            //     new Style({
            //         image: new CircleStyle({
            //             radius: 6,
            //             fill: new Fill({
            //                 color: place.found ? '#F92880' : '#F91565',
            //             }),
            //             stroke: new Stroke({
            //                 color: '#fff',
            //                 width: 2,
            //             }),
            //         }),
            //     }));
            // console.log(vectorLayer)
            this.map.addLayer(this.targetPlaceLayer);
        },
    },
    mounted () {
        this.initMap();
    }
}
</script>

<style scoped>

.map {
    width: 100%;
    height: 100vh;
}


#map:focus {
outline: #4A74A8 solid 0.15em;
}

</style>