import { createApp } from 'vue';
import { createStore } from 'vuex';
import App from './App.vue';


const store = createStore({
    state: () => ({
        focusPosition: true,
        currentLocation: {
            position: {
                lat: 0,
                lon: 0,
            },
            heading: 0,
            features: {
                name: '',
                street: '',
                city: '',
                county: '',
                state: '',
                country: '',
            },
            name: '',
        },
        currentView: 'listview',
        viewHistory: ['listview'],
        places: {},
    }),
    mutations: {
        startFollowingPosition (state) {
            state.focusPosition = true;
        },
        stopFollowingPosition (state) {
            state.focusPosition = false;
        },
        updateLocation (state, newLocation) {
            state.currentLocation = {...state.currentLocation, ...newLocation};   // Update properties for locatino
        },
        changeView (state, payload) {
            if (payload.reset) {
                state.viewHistory = ['listView'];
            } else {
                state.viewHistory.push(state.currentView);
            }
            state.currentView = payload.view || payload ;
        },
        backView (state) {
            state.currentView = state.viewHistory.pop();
        },
        addPlace (state, newPlace) {
            if (newPlace.current !== undefined) {
                state.places[newPlace.current.id] = {...state.places[newPlace.current.id], ...newPlace.current};
            }
            if (newPlace.next !== undefined) {
                state.places[newPlace.next.id] = newPlace.next;
            }
        },
        loadPlaces (state, data) {
            state.places = data;
        },
    },
});

const app = createApp(App);

app.use(store);
app.mount('#app')