<template>
  <div id="container">
    <div
      id="list-view"
      :class="{view: true}">

      <div class="scroll-container">
        <div class="list-container">
          <empty-space v-if="placesArray.length == 0">
            Tocca sull'icona della mappa per iniziare ad esplorare
            </empty-space>
          <place-list
            v-else :array="placesArray"
            @focus-place="{
              $refs.map.focusPlace($event);
              changeView('mapview');
              }"></place-list>
        </div>
        <div class="title">
            <span class="text-container">
                I tuoi luoghi
            </span>
        </div>

      </div>
      <floating-button
          :style="{bottom: 5+'vh', right: 5+'vw'}"
          src="map.svg"
          @click="changeView('mapview')"></floating-button>
    </div>
    <div
      id="map-view"
      :class="{view: true, 'hidden-right': currentView === 'listview'}">
      <div class="title over-map">
          <img id="back-button" @click="backView()" src="@/assets/arrow-left.svg">
          <span class="text-container">
              {{currentLocation.name}}
          </span>
      </div>
      <floating-button
          :style="{bottom: 5+'vh', left: 5+'vw'}"
          v-show="!focusPosition"
          src="crosshairs-gps.svg"
          @click="startFollowingPosition()"
          class="over-map"></floating-button>
      <floating-button
          :style="{bottom: 5+'vh', right: 5+'vw'}"
          src="qrcode-scan.svg"
          @click="changeView('scannerview')"
          class="over-map"></floating-button>
      <open-layers-map ref="map" :place-data="currentPlace"></open-layers-map>
    </div>
    <div
      id="code-view"
      :class="{view: true, 'hidden-right': (currentView === 'mapview' || currentView === 'listview')}">
      <div class="qr-background">
        <qr-scanner
          :run="!(currentView === 'mapview' || currentView === 'listview')"
          @found="qrResult = $event"></qr-scanner>
        <div class="over-scanner">
          <span id="qr-message">{{qrMessage}}</span>
          <floating-button
              :style="{top: 5+'vh', left: 5+'vw'}"
              src="arrow-left.svg"
              @click="backView()"
              ></floating-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import OpenLayersMap from './components/OpenLayersMap.vue';
import FloatingButton from './components/FloatingButton.vue';
import QrScanner from './components/Qr-Scanner.vue';
import EmptySpace from './components/EmptySpace.vue';
import PlaceList from './components/PlaceList.vue';

export default {
  name: 'App',
  components: {
    OpenLayersMap,
    FloatingButton,
    QrScanner,
    EmptySpace,
    PlaceList,
  },
  emits: ['newCode'],
  data () {
    return {
      currentMapLocationName: "",
      qrResult: "",
      qrMessage: "Codice non trovato",
      currentPlace: {},
      // places: [{title:'places'}, {title:'uno'}, {title:'due'}, {title:'tre'},{title:'places'}, {title:'uno'}, {title:'due'}, {title:'tre'}],
    }
  },
  watch: {
    qrResult (newValue) {
      if (newValue) {
        this.handleCode(newValue);
        this.qrMessage = "Codice non trovato";
      }
    },
    currentView (newValue) {
      if (newValue === 'scannerview') {
        this.qrResult = "";
        this.qrMessage = "Codice non trovato";
      }
    },
    places: {
      handler (newValue) {
        localStorage.places = newValue;
      },
      deep: true,
    },
  },
  computed: {
    placesArray () {
      return Object.values(this.places);
    },
    ...mapState([
      'focusPosition',
      'currentLocation',
      'currentView',
      'viewHistory',
      'places',
    ]),
  },
  methods: {
    ...mapMutations([
      'startFollowingPosition',
      'changeView',
      'backView',
      'addPlace',
      'loadPlaces',
    ]),
    handleCode (value) {
      try {
        value = JSON.parse(value);
      } catch (e) {
        console.log(e);
      }
      if (value.data.id === 'appid'){     // Codice valido?
        if (this.places[value.data.id] === undefined) {                // Codice gia usato?
          this.addPlace(value.data);
          this.$refs.map.focusPlace(value.data.next);
        } else {
          this.qrMessage = "Codice gia inserito!";
          this.$refs.map.focusPlace(value.data.current);
        }
        this.backView();
      }
    },
  },
  // mounted () {
  //   localStorage.places = undefined;
  //   if (localStorage.places !== undefined) {
  //     this.loadPlaces(localStorage.places);
  //   }
  // },
}
</script>

