<template>
    <video id="scanner" ref="video">
        prova
    </video>
</template>

<script>
import QrScanner from 'qr-scanner';
QrScanner.WORKER_PATH = '@/qr-scanner-worker.min.js';

export default {
    props: ['run'],
    data () {
        return {
            scanner: {},
        }
    },
    emits: ['found'],
    watch: {
        run (newValue) {
            if (newValue) {
                this.scanner.start();
            } else {
                this.scanner.stop();
            }
        }
    },
    methods: {
        setupScanner () {
            const scanner = new QrScanner(
                this.$refs['video'],
                result => {
                    this.$emit('found', result);
                    // console.log(result);
                });
            scanner.start();
            this.scanner = scanner;
        }
    },
    mounted () {
        this.setupScanner();
    },
}
</script>

<style scoped>

#scanner {
    position: relative;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    object-fit: cover;
}
</style>